.posts-eventos{
	
	.posts{
		justify-content: space-between;

		div[class^=mypost-]{
			position: relative;
			flex: 0 0 49%;
			margin-bottom: 30px;

			.image-container{
				display: flex;
    			align-items: flex-end;
				min-height: 350px;
				background-position: center;
				background-repeat: no-repeat;
				background-size: 140%;
				transition: all 0.2s linear;

				&:hover{
				   	background-size: 150%;
				}									

				.card-info{
					background-color: rgba(206, 206, 206, 0.75);
				    margin: 0 30px;
				    margin-bottom: 15px;
				    padding: 10px 20px;

					.fecha{
						text-transform: uppercase;
					    font-size: 12px;
					    font-weight: 500;
					}

					.categorias{
						text-transform: uppercase;
					    font-weight: 500;
					    margin-bottom: 15px;
					    color: $mainColor;

						a{}
					}

					.titulo{
						font-weight: 700;

						a{
							color: $texto;
						}
					}
				}
			}
		}
	}
}


@media(max-width:768px){
	.posts-eventos .posts div[class^=mypost-]{
		flex: 0 0 100%;
	}
	.filtros-eventos{
		.container-custom{
			padding: 0;

			.all-selects{

				.container-select {
    				flex: 0 0 25%;

    				select{
    					font-size: 14px;
    				}
    			}
			}
		}
	}
}