.bloque-texto-foto{
	width: 100%;
	padding: 50px 0;

	.izq{
		display: flex;
    	align-items: center;

    	.botones-stores{
    		display: flex;
		    justify-content: space-between;
		    padding: 20px 0;

		    .boton-cupa-granate{
		    	padding-top: 0;
		    }

		    a{

		    	img{
		    		max-width: 150px;
    				margin-right: 20px;
		    	}

		    	&:last-child{

		    		img{			    	
	    				margin-right: 0;
			    	}
		    	}
		    }
    	}
	}

	.der{
		display: flex;
    	align-items: center;
    	padding: 0;
    	overflow: hidden;
    	
		.container-foto{
			width: 130%;
			margin: 0 auto;
			position: relative;
    		right: 0;
    		display: flex;
    		align-items: center;

			img{}

			.imagen-central{
				opacity: 1;
				position: absolute;
			    top: 50%;
			    left: 50%;
			    transform: translate(-50%, -50%);
			    max-width: 270px;
			}
		}
	}
}


@media(max-width:1200px){
	.bloque-texto-foto {
		.izq .botones-stores{
			flex-direction: column;
		}
	}
}

@media(max-width:767px){
	.bloque-texto-foto {
		.der .container-foto{
			margin-top: 20px;
		}
	}
}