.bloque-logos{
	padding-top: 30px;

	.container-logos{
		display: flex;
	    justify-content: space-around;
	    align-items: center;
	    flex-wrap: wrap;	    
	    border-top: 1px solid #d0d0d0;
	    border-bottom: 1px solid #d0d0d0;

		div[class^=logo-]{

			img{
				padding: 10px 5px;
				max-width: 250px;
			}
		}

		.separa{
			width: 1px;
		    min-height: 120px;
		    background-color: #d0d0d0;
		}
	}
}

@media(max-width: 1280px){
	.bloque-logos .container-logos div[class^=logo-] img{
		max-width: 150px;
	}
}

@media(max-width:768px){
	.bloque-logos .container-logos {
		border-bottom: unset;
		div[class^=logo-]{
			flex: 0 0 100%;
			text-align: center;
			border-bottom: 2px solid #d0d0d0;
			img{
				max-width: 250px;
			}
		}
		.separa{
			display: none;
		}
	}
}