.bloque-cuando-donde{
	max-width: 950px;
	margin: 0 auto;
    padding: 0 15px;	

    .container-blog{
    	display: flex;
	    justify-content: space-between;
	    padding-top: 20px;
	    border-bottom: 1px solid $mainColor;

	    .cuando-donde{
	    	display: flex;
		    flex-direction: column;
		    justify-content: center;

		    .cuando{
		    	margin-bottom: 20px;
		    	border-left: 4px solid $mainColor;	
		    	padding-left: 10px;	    	
		    }

		    .donde{
		    	border-left: 4px solid $mainColor;
		    	padding-left: 10px;
		    }

		    span.titulo{
		    	font-weight: 600;
		    }

		    span.texto{
		    	font-weight: 500;
		    	color: $mainColor;
		    	margin-left: 10px;
		    }
	    }
    }
}


.form-asistir{
	border-top: 1px solid $mainColor;
    padding-top: 20px;
    margin-top: 40px;

	form{
		background-color: #F5F5F5;
    	padding: 15px;
    	margin-top: 15px;

		.form-cupa{
			padding: 20px 0;

			.row{

				h2{
					text-transform: uppercase;
				    font-size: 16px;
				    font-weight: 600;
				    margin-bottom: 20px;
				}

				.dos-col{
					display: flex;
   					justify-content: space-between;
   					padding: 0;

					label{
						width: 49%;
					}
				}

				.una-col{
					display: flex;
				    align-items: center;
				    justify-content: flex-end;
				    padding: 0;

					label{}
				}

				.boton-cupa-granate{
					position: unset;
					right: unset;
					bottom: unset;

					a{
						pointer-events: all;

						input{
							pointer-events: all;
						}

						.ajax-loader{
							display: none;
						}
					}
				}
			}
		}

		div.wpcf7-validation-errors, div.wpcf7-acceptance-missing{
			text-align: center;
		}
	}
}



@media(max-width:768px){
	.bloque-contenido-single-evento .form-asistir form .form-cupa .row .dos-col{
		display: block;

		label{
			width: 100%;
		}
	}
}

@media(max-width:480px){
	.bloque-cuando-donde .container-blog{
		display: block;
	}
	.bloque-contenido-single-evento .form-asistir form .form-cupa .row .una-col{
		justify-content: center;
	}
}