.posts-category{

	.container-custom{
		display: flex;
    	flex-wrap: wrap;

    	.container-posts-2-estilos{
    		order: 3;
    	}

    	.leer-mas{
    		order: 2;
    		width: 100%;
    		text-align: center;
			font-size: 18px;			
			color: $mainColor;
			cursor: pointer;
			text-decoration: underline;
			font-weight: 300;
			text-transform: uppercase;
			margin-bottom: 30px;
    	}

    	.container-tags{
    		order: 1;
    		width: 100%;
		    display: flex;
		    align-items: flex-start;
    		flex-wrap: wrap;
		    margin-bottom: 5px;
		    max-height: 100px;
		    overflow: hidden;
		    position: relative;

		    div[class^=etiqueta-]{
		    	padding: 5px 15px;
			    position: relative;
			    text-transform: capitalize;
			    font-weight: 600;
			    background-color: #F5F5F5;
			    cursor: pointer;
			    margin-right: 20px;
    			margin-bottom: 20px;
    			background-image: url(../images/tag_black.png);
    			background-repeat: no-repeat;
			    background-position: 10px center;
			    padding-left: 40px;
			    background-size: 18px 18px;
			    transition: all 0.4s ease;

			    &:hover{
			    	background-color: $mainColor;
			    	background-image: url(../images/tag_white.png);
			    	color: #FFF;
			    }

			    &.active{
			    	background-color: $mainColor;
			    	background-image: url(../images/tag_white.png);
			    	color: #FFF;
			    }
		    }

		    .fadeout{
    			position: absolute; 
			    bottom: 0em;
			    width: 100%;
			    height: 4em;
			    background: -webkit-linear-gradient(
			        rgba(255, 255, 255, 0) 0%,
			        rgba(255, 255, 255, 1) 100%
			    ); 
			    background-image: -moz-linear-gradient(
			        rgba(255, 255, 255, 0) 0%,
			        rgba(255, 255, 255, 1) 100%
			    );
			    background-image: -o-linear-gradient(
			        rgba(255, 255, 255, 0) 0%,
			        rgba(255, 255, 255, 1) 100%
			    );
			    background-image: linear-gradient(
			        rgba(255, 255, 255, 0) 0%,
			        rgba(255, 255, 255, 1) 100%
			    );
			    background-image: -ms-linear-gradient(
			        rgba(255, 255, 255, 0) 0%,
			        rgba(255, 255, 255, 1) 100%
			    );
    		}
    	}
	}
	
	.grupo-impar{

		.row{
			margin-bottom: 30px !important;
		}

		.resto-de-posts{
			position: relative;							
			width: 33.33%;
			float: left;

			.image-container{
				display: flex;
    			align-items: flex-end;
				min-height: 280px;
				background-position: center;
			    background-repeat: no-repeat;
			    background-size: 140%;
			    margin-right: 15px;
			    margin-bottom: 30px;
			    transition: all 0.2s linear;

			    &.custom-margins{
			    	margin-right: 0!important;
    				margin-left: 15px;
			    }

			    &:hover{
			    	background-size: 150%;
			    }									

				.card-info{
					background-color: rgba(206, 206, 206, 0.75);
				    margin: 0 30px;
				    margin-bottom: 15px;
				    padding: 10px 20px;

					.fecha{
						text-transform: uppercase;
					    font-size: 12px;
					    font-weight: 500;
					}

					.categorias{
						text-transform: uppercase;
					    font-weight: 500;
					    margin-bottom: 15px;

						a{}
					}

					.titulo{
						font-weight: 700;
						line-height: 20px;

						a{
							color: $texto;
						}
					}
				}
			}
		}
	}
}

@media (max-width: 1024px){
	.posts-category .grupo-impar .row{
		margin-bottom: 0 !important;
	}

	.posts-category .container-posts-2-estilos div[class^=grupo-] .inner .inf .container-posts .row div[class^=mypost-]{
		padding-bottom: 0px;
		margin-bottom: 0 !important;
	}

	.posts-category .grupo-impar .resto-de-posts{
		width: 100% !important;

		.image-container{
			margin-right: 0 !important;
			margin-left: 0 !important;
		}
	}
}

@media (max-width: 768px){
	.posts-category .container-custom .container-tags div[class^=etiqueta-]{
		font-size: 10px;
		margin-right: 8px;
    	margin-bottom: 8px;
	}
}