.bloque-foto-texto{
	width: 100%;
	margin-bottom: 50px;
	
	.row{

		.izq{
			padding: 0;

			.container-foto{
				position: relative;

				> img{}

				.imagen-superpuesta{
					position: absolute;
					right: -30px;
					bottom: -30px;
					background-color: #FFF;
				    border-radius: 50%;
				    max-width: 250px;
				    box-shadow: 4px 4px 20px 0px #bbbbbb;

				    img{
				    	opacity: 1;
				    	max-width: 100%;
    					padding: 15px;
				    }
				}
			}
		}

		.der{
			display: flex;
  			align-items: center;
  			justify-content: center;

  			.container-texto{
  				padding-top: 150px;
  			}
		}
	}

	&.bloque-foto-texto-stonepanel{

		.container-foto{

			.imagen-superpuesta{
				display: none !important;
				width: 250px;
				height: 250px;
				display: flex;
			    align-items: center;
			    flex-wrap: wrap;
			    align-content: center;
			    top: 50%;
			    right: -10px;
			    bottom: unset;
			    transform: translate(0, -50%);

				img{
					max-width: 90%;
					margin: 0 auto;

					&.pseudoslider{
    					opacity: 0;
    					display: none;
    					transition: all .3s ease;

    					&:first-child{
    						opacity: 1;
    						display: inline;
    					}
    				}
				}

				.titulo{
					width: 100%;
					text-align: center;
				}
			}
		}

		.container-texto{

			.imagen-superior{
				overflow: hidden;

				p{
					margin-bottom: 0;
					position: relative;

					img{
						max-width: 300px;
					}
				}								
			}

			.descripcion{
				/*font-size: 44px;*/
			    /*font-weight: 400;*/
			    padding: 30px 0;
			}

			.boton-cupa-granate{
				/* QUITAMOS BOTÃN NARANJA */
				/*
				a{
					color: $naranja;
					border-left: 1px solid #F18A04;
    				border-right: 1px solid #F18A04;

					.arrow-btn{
						background-image: url(../images/right-arrow-naranja.png);
					}

					&:hover{
      					border-top: none;
    					border-bottom: none;
      				}

					&:before{
						background-color: $naranja;
					}

					&:after{
						background-color: $naranja;
					}
				}
				*/
			}
		}
	}
}


@media(max-width:1440px){
	.bloque-foto-texto .row .izq .container-foto .imagen-superpuesta{
		max-width: 250px;
	}
}

@media(max-width:1024px){
	.bloque-foto-texto .row .izq .container-foto .imagen-superpuesta{
		max-width: 180px;
		right: unset;
	    left: 50%;
	    transform: translate(-50%, 0);
	}
}

@media(max-width:768px){
	.bloque-foto-texto {
		.row .der .container-texto{
			padding-top: 30px;
		}

		&.bloque-foto-texto-stonepanel{
			.izq{
				flex: 0 0 100%;
			    max-width: 100%;
			    order: 2;
			}

			.der{
				flex: 0 0 100%;
			    max-width: 100%;
			    order: 1;
			}
		}
	}
}

@media(max-width:767px){
	.bloque-foto-texto .row {
		display: flex;

		.izq {
			order: 2;

			.container-foto .imagen-superpuesta{
				max-width: 290px;
		    	left: 50%;
		    	-webkit-transform: translate(-50%, 0%);
		    	transform: translate(-50%, 0%);
			}
		}

		.der{
			order: 1;
		}
	}
}

@media(max-width:767px){
	.bloque-foto-texto .row .der .container-texto{
		padding-top: 0px;
	}	
}

@media(max-width:480px){
	.bloque-foto-texto {
		margin-bottom: 100px;

		.row .izq .container-foto .imagen-superpuesta{
			bottom: -100px;
		}
	}
}