.bloque-contacto{
	padding-top: 30px;

	.container-contacto{
		display: flex;
	    align-items: center;
	    justify-content: space-between;

		.cupastone-map{
			width: 100%;
			height: 650px;
			flex: 0 0 50%;
		}

		.formulario-contacto{
			flex: 0 0 50%;
			background-color: unset;
    		padding: 2% 5% 2% 4%;

			.form-contacto{
				position: relative;
				max-width: 600px;
    			margin: 0 auto;

				h2{
					text-transform: uppercase;
				}

				textarea{
					min-height: 60px;
				    background-color: transparent;
				    border: none;
				    border-bottom: 1px solid #bdbdbd;
				    border-radius: 0;
				    outline: 0;
				    width: 100%;
				    font-size: 0.8rem;
				    box-shadow: none;
				    box-sizing: content-box;
				    -webkit-transition: all .3s;
				    transition: all .3s;

				    &:focus{
				    	border-bottom: 1px solid $mainColor;
    					box-shadow: 0 1px 0 0 $mainColor;
				    }
				}

				.boton-cupa-granate {
				    padding: 0;
				    float: right;
				    padding-top: 10px;
				    position: absolute;
				    right: 0;
				    bottom: -40px;

				    a{
				    	pointer-events: all;

				    	input{
				    		pointer-events: all;
				    	}

				    	.ajax-loader{
				    		/*display: none !important;*/
				    	}
				    }
				}
			}
		}
	}
}


@media(max-width:1024px){
	.bloque-contacto .container-contacto {
		display: unset;
		.cupastone-map{
			height: 400px;
		}
		.formulario-contacto{
			padding: 30px;
		}
	}
}