.bloque-gama-completa{
	padding-top: 30px;

	.titulos{
		padding: 40px 10px;
    	width: 100%;
		background-color: #FFF;

		.sup{
			text-align: left;
		    margin-bottom: 10px;
		    text-transform: uppercase;
		}

		.inf{
			text-align: left;
		    font-size: 40px;
		    font-weight: 400;
		    line-height: 1.2;

		    p{
		    	margin: 0;
		    }
		}
	}

	.container-slider{
		background-color: #E2E2E2;
    	padding: 10px;
    	background-repeat: no-repeat;
	    background-position: bottom right;
	    background-origin: content-box;

	    .tns-outer{
	    	margin: 20px 10px;
	    }

    	div[class^=myslider-]{
    		visibility: hidden;

	    	div[class^=producto-]{
			    padding: 20px;
	    		
	    		a{

	    			.inner-producto{
	    				display: flex;
					    align-items: center;
					    flex-wrap: wrap;
					    flex-direction: column;
					    justify-content: center;
					    align-content: center;
					    background-color: rgba(255, 255, 255, 0.70);
					    border-radius: 50%;
					    box-shadow: unset;
					    transition: all 0.4s ease;

					    &:hover{
					    	box-shadow: 4px 4px 20px 0px #bbbbbb;
					    }

	    				img{
	    					margin: 0 auto;
	    					max-width: 80%;
	    					margin-bottom: 10px;
	    				}

	    				.titulo{	    			
    						text-align: center;
						    max-width: 80%;
						    margin: 0 auto;
	    				}
	    			}
	    		}
	    	}
	    }

	    &.no-slider{

	    	div[class^=myslider-]{
	    		display: flex;
	    		align-items: center;
	    		flex-wrap: wrap;

		    	div[class^=producto-]{
		    		flex: 0 0 20%;

		    		a{

		    			.inner-producto{

		    				img{}

		    				.titulo{}
		    			}
		    		}
		    	}
		    }
	    }
	}
}


@media(max-width:768px){
	.bloque-gama-completa .container-slider{
		background-size: 250px;
	}
}

@media(max-width:767px){
	.bloque-gama-completa .container-slider div[class^=myslider-] div[class^=producto-] {
	    position: relative;
	    left: -10px;
	}
}