
.bloque-cabecera-single-post{
	min-height: 60vh;
	background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: flex-end;
    justify-content: center;

    .container-blog{
    	max-width: 950px;
        min-width: 950px;
        margin: 0 auto;
        padding: 0 15px;
        z-index: 1;
        
    	h1{
    		font-size: 55px;
		    color: #FFF;
		    font-weight: bold;
		    margin-bottom: 30px;
            line-height: 1;
    	}
    }

    .velo-cabecera-single-post{
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.20);
        z-index: 0;
    }
}


.bloque-contenido-single-post{
	display: flex;
    justify-content: center;
    padding: 20px 0;
	
	.container-blog{
    	max-width: 950px;
    	padding: 0 15px;

    	.top{
    		padding-top: 20px;
    		padding-bottom: 30px;

    		.fecha{
    			text-transform: uppercase;
    			font-weight: 400;
    			font-size: 14px;
    		}

    		.categorias{
    			text-transform: uppercase;
			    font-weight: 500;
			    font-size: 14px;
    		}
    	}

    	.contenido{
    		font-weight: 500;
    	}
    }
}



@media(max-width:1024px){
    .bloque-cabecera-single-post .container-blog{
        min-width: unset;
    }
}

@media(max-width:768px){
    .bloque-cabecera-single-post .container-blog h1 {
        font-size: 36px;
    }
}