.bloque-galerias{
	padding: 30px 0;
	
	.container-custom{

		.container-galerias{
			display: flex;
		    align-items: center;
		    justify-content: center;

			div[class^=galeria-]{
				flex: 0 0 50%;
				padding: 30px;

				.nombre{
					font-size: 40px;
    				
    				a{
    					font-weight: 300;
    					transition: all 0.4s ease;
    				}    				
				}

				a{				
					.imagen{
						overflow: hidden;
						background-size: 100%;
					    background-repeat: no-repeat;
					    background-position: center center;
					    opacity: 1;
					    transition: all 0.4s ease;
					    max-height: 540px;

					    &:hover{
					    	background-size: 110%;
					    	opacity: 0.8;
					    }
						
						img{
							visibility: hidden;
						}
					}
				}
			}
		}
	}
}


@media(max-width:767px){
	.bloque-galerias .container-custom .container-galerias{
		flex-direction: column;
		div[class^=galeria-]{
			flex: 0 0 100%;
			padding: 30px 0;
		}
	}
}