.bloque-hero-home{
	width: 100%;
	height: 100vh;
	overflow: hidden;

	.container-hero{
		display: flex;
		flex-wrap: wrap;
		position: relative;

		.fondos{
			width: 100%;
			height: 100vh;
			position: absolute;
		    top: 0;
		    left: 0;
			background-position: center center;
			background-repeat: no-repeat;
			background-size: cover;

	    	div[class^=fondo-]{	    		
	    		width: 100%;
				height: 100vh;
				background-position: center center;
				background-repeat: no-repeat;
				background-size: cover;				
	    	}
		}
		
    	.cajas{
    		display: flex;
		    flex-wrap: wrap;
		    width: 100%;

			div[class^=caja-]{
				width: 25%;
				height: 50vh;
				position: relative;
				display: flex;
	    		align-items: center;			
	    		position: relative;
	    		border: 1px solid rgba(255, 255, 255, 0.2);


	    		.texto-caja1{
	    			margin-left: 10px;
				    font-size: 70px;
				    line-height: 1.2;
				    color: #FFFFFF;
				    font-weight: 100;

	    			span {
					  border-bottom: 2px solid $mainColor;
					}
	    		}

				.texto-inferior{
					position: absolute;
				    bottom: 10px;
				    right: 10px;
				    color: #FFF;
				    font-size: 1.2rem;
				    text-transform: uppercase;
				    max-width: 250px;
				    text-align: right;
				}

				.div-hover{
					display: none;
					width: 100%;
					height: 50vh;    				
					position: absolute;
					top: 0;
					left: 0;
					padding: 10%;
					flex-direction: column;
				    align-items: flex-start;
				    justify-content: center;
					transition: all 0.3s ease;

					.cabecera{
						font-size: 15px;
						font-weight: 100;
						color: #FFF;
						text-transform: uppercase;
						text-align: left;
						padding-top: 15%;
					}

					.titulo{
						font-size: 25px;
						font-weight: 500;
						color: #FFF;
						text-transform: uppercase;
						text-align: left;
						margin-bottom: 20px;
					}

					.contenido{
						font-size: 15px;
						font-weight: 100;
						color: #FFF;
						text-align: left;
					}

					.aspa{
						position: absolute;
						bottom: -5px;
						right: 15px;
						font-size: 3rem;

						&:before{
							content: '\002B';
							color: #ffffff9e;
						}
					}
				}

				&:hover{
					.texto-inferior{
						display: none;
					}

					.div-hover{
						display: flex;
					}
				}
			}

			.caja-2{
				.div-hover{
					background-color: rgba(238, 134, 116, 0.7);
				}
			}

			.caja-3{
				.div-hover{
					background-color: rgba(109, 195, 213, 0.7);
				}
			}

			.caja-4{
				.div-hover{
					background-color: rgba(98, 26, 125, 0.7);
				}
			}

			.caja-5{
				.div-hover{
					background-color: rgba(111, 29, 70, 0.7);
				}
			}

			.caja-6{
				.div-hover{
					background-color: rgba(102, 206, 102, 0.7);
				}
			}

			.caja-7{
				.div-hover{
					background-color: rgba(188, 57, 127, 0.7);
				}
			}

			.caja-8{
				display: flex;
			    align-items: center;
			    justify-content: center;			    
			}
		}		
	}
}

@media(max-width: 1440px){
	.bloque-hero-home {
		.container-hero {
			.cajas {
				div[class^=caja-] {
					.texto-caja1{
						font-size: 4rem;
					}

					.div-hover{
						.titulo{
							font-size: 1.5rem;
						}
					}
				}
			}
		}
	}
}

@media(max-width: 1024px){
	.bloque-hero-home {
		height: 200vh;
		.container-hero {
			height: 200vh;
			.fondos{
				height: 200vh;
			}
			.cajas {
				div[class^=caja-]{
					width: 50%;
				}
			}
		}

		.velo-completo-negro{
			height: 200vh;
		}
	}
}

@media(max-width: 768px){
	.bloque-hero-home {
		.container-hero .cajas div[class^=caja-] .texto-caja1{
			display: none;
		}
	}	
}

@media(max-width: 480px){
	.bloque-hero-home {
		height: 100vh;
		.container-hero {
			height: 100vh;
			.fondos{
				height: 100vh;
			}
			.cajas {				
				div[class^=caja-]{
					height: 25vh;
					width: 50%;

					.div-hover{
						height: 25vh;

						.cabecera{
							margin-top: 15px;
							font-size: 14px;
						}

						.titulo{
							font-size: 18px;
						}

						.contenido{
							display: none;
						}
					}

					.boton-cupa-blanco{
						max-width: 150px;

						a{
							font-size: 10px;

							.arrow-btn{
								margin-left: 5px;
							}
						}
					}
				}
			}
		}
		.velo-completo-negro{
			height: 100vh;
		}
	}
}