.bloque-texto-video{
	padding: 30px 0;

	.container-custom{
		position: relative;
		display: flex;
    	align-items: center;

    	.row{
    		width: 100%;

	    	.container-video{
				position: relative;
				width: 100%;
				max-width: 100%;
				height: 415px;
				pointer-events: none;
				overflow: hidden;

				iframe{
					position: absolute;
		    		top: -60px;		    		
					width: 100%;
					max-width: 850px;
					height: 480px;
				}
			}
		}

		.container-texto{
	    	position: absolute;
	    	top: 30px;
	    	padding: 30px 70px;
			background-color: $naranjaOpacity80;

			.subtitulo{}

			.titulo{
				font-size: 40px;
				font-weight: 400;
				margin-top: 0;
			}

			.descripcion{
				font-size: 16px;
				font-weight: 300;
			}
		}
	}
}

@media(max-width:768px){
	.bloque-texto-video{
		
		.container-custom{
			height: 100%;
			align-items: unset;
			flex-wrap: wrap;

			.row{
				height: 100%;
				order: 2;

				.outer-video{
					flex: 0 0 100%;
				    max-width: 100%;
				    margin-left: 0;
				    display: flex;
				    align-items: flex-end;

				    .container-video{
				    	pointer-events: unset;
				    	height: 360px;
				    }
				}
			}

			.container-texto{
				position: relative;
				width: 100%;
    			max-width: 100%;
    			order: 1;
    			padding: 20px;
    			padding-bottom: 50px;

    			.titulo{
					font-size: 34px;
    			}
			}
		}
	}
}

@media(max-width:660px){
	.bloque-texto-video{		
		.container-custom{			
			.row{
				.outer-video{
				    .container-video{				    
				    	height: 280px;
				    	iframe{
				    		height: 320px;
				    	}
				    }
				}
			}
		}
	}
